import React from "react";
import { ZroundingButton ,zTool} from "zerod";
const {addClass, removeClass}=zTool;
import { Row, Col, Button, Icon, message } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC } = HOC;
import doc from "zerod/components/ZroundingButton/doc.md";
export default AmdDocHOC(doc, {
	demo1: function() {
		class Myjavascript extends React.PureComponent {
			onClick = (item) => {
				message.success("点击了" + item.name);
			};
			items = [
				{
					name: "按钮1",
					icon: () => <i className="zero-icon zerod-shengchangzhouqi" />,
					onClick: this.onClick,
				},
				{ name: "按钮2", icon: <Icon type="star" theme="filled" />, onClick: this.onClick },
				{ name: "按钮3", icon: () => <Icon type="eye" theme="filled" />, onClick: this.onClick },
				{ name: "按钮4", icon: <Icon type="camera" theme="filled" />, onClick: this.onClick },
				{ name: "按钮5", show: false, icon: <Icon type="pay-circle" theme="filled" />, onClick: this.onClick },
				{
					name: "按钮6",
					disabled: true,
					icon: <Icon type="hourglass" theme="filled" />,
					onClick: this.onClick,
				},
			];
			onVisibleChange = (show) => {
				if (this.moreIconEl) {
					show ? removeClass(this.moreIconEl, "is-down") : addClass(this.moreIconEl, "is-down");
				}
			};
			render() {
				return (
					<span>
						<ZroundingButton items={this.items}>
							<Button size="small">open</Button>
						</ZroundingButton>
						<ZroundingButton
							items={this.items}
							className="z-margin-left-80"
							onVisibleChange={this.onVisibleChange}
						>
							<Button size="small" type="primary" className="z-btn-green">
								open
								<i
									className="zero-icon zerod-up z-open-btn is-down z-margin-left-4"
									ref={(el) => {
										this.moreIconEl = el;
									}}
								/>
							</Button>
						</ZroundingButton>
					</span>
				);
			}
		}
		return <Myjavascript />;
	},
});
